import Swiper from 'swiper/bundle';

export const ConsequencesSwiper = () => {
  const swiper = new Swiper('.consequences .swiper', {
    spaceBetween: 0,
    direction: 'horizontal',
    slidesPerView: 'auto',
    scrollbar: {
      el: '.consequences .swiper-scrollbar',
      draggable: true,
    },
    breakpoints: {
      1200: {
        slidesPerView: 'auto',
        spaceBetween: 0,
      },
      600: {
        slidesPerView: 3,
        spaceBetween: 12,
      },
      0: {
        scrollbar: {
          el: '.consequences .swiper-scrollbar',
          hide: false,
          draggable: true,
        },
        slidesPerView: 'auto',
        spaceBetween: 0,
      },
    },
  });
}
