import {SwiperComponent} from "./SwiperComponent";
import {BurgerMenu} from "./components/BurgerMenu";
import {initIphoneModal} from "./components/Modals";
import {HeaderSubmenu} from "./components/HeaderSubmenu";
import {Switcher} from "./components/Switcher";
import {BankruptcySeeMoreBtn} from "./components/BankruptcySeeMoreBtn";
import {ReviewsSwiper} from "./components/ReviewsSwiper";
import {SwiperCurrentRates} from "./components/SwiperCurrentRates";
// import {ArticlesSwiper} from "./components/ArticlesSwiper";
// import {ArticlesTabs} from "./components/ArticlesTabs/ArticlesTabs";
import {ArticlesPage} from "./components/ArticlesPage/ArticlesPage";
import {CurrentRatesSection} from "./components/CurrentRatesSection/CurrentRatesSection";
import {Form} from "./components/Form";
import {ServicesList} from "./components/ServicesList";
//      AnswerQuestions
// import {FormAnswerQuestions} from "./components/FormAnswerQuestions";
import {PlanBlock} from "./components/PlanBlock";
import {ReputationBlock} from "./components/ReputationBlock";
import {ConsequencesSwiper} from "./components/SwiperConsequences";
import {VideoModal} from "./components/VideoModal";

import IMask from "imask";

//   
import { createServer, Response } from "miragejs";


function main() {
  window.modal = initIphoneModal();
  // window.modal.open('#consultationModal');

  //  safari,   vh
  let vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty('--vh', `${vh}px`);

  // Form();
  SwiperComponent();
  BurgerMenu();
  HeaderSubmenu();
  Switcher();
  ArticlesPage();
  CurrentRatesSection();
  BankruptcySeeMoreBtn();
  ReviewsSwiper();
  ServicesList();
  SwiperCurrentRates();
  //      AnswerQuestions
  // FormAnswerQuestions();
  PlanBlock();
  ConsequencesSwiper();
  ReputationBlock();
  VideoModal();

  const initPrimaryInput = ($inputWrapper) => {
    const $input = $inputWrapper.querySelector('.input');
    const checkInput = () => {
      if ($input.value.length > 0) {
        $inputWrapper.classList.add('filled');
      } else {
        $inputWrapper.classList.remove('filled');
      }
    };

    checkInput();

    $input.addEventListener('input', () => {
      checkInput();
      $inputWrapper.classList.remove('error');
    });

    $input.addEventListener('change', () => {
      checkInput();
    });
  }

  const inputs = document.querySelectorAll('input');
  inputs.forEach((inputsItem) => {
    const $inputContainer = inputsItem.closest('.input-container');
    if ($inputContainer) initPrimaryInput($inputContainer);
  });

  document.querySelectorAll('.consultation-modal_form_input').forEach(($input) => {initPrimaryInput($input.parentElement)});

  //        
  const telInputsList = document.querySelectorAll('[data-tel-input]');
  const maskOptions = {
    mask: '+{7} (000) 000-00-00'
  };

  //   
  const formsConsultationList = document.querySelectorAll('form[data-form="consultation"]');

  formsConsultationList.forEach($form => {
    const customLoaderColor = $form.getAttribute('data-form-loader-color');
    new Form($form, {
      onResponseSuccess: (response, form) => {
        form.clearInputs();
        window.modal.open('#successModal');
      },
      loaderColor: (customLoaderColor) ? customLoaderColor : '#fff'
    })
  })

  const formsAskQuestionList = document.querySelectorAll('form[data-form="ask-question"]');

  formsAskQuestionList.forEach($form => {
    const customLoaderColor = $form.getAttribute('data-form-loader-color');
    new Form($form, {
      onResponseSuccess: (response, form) => {
        form.clearInputs();
        window.modal.open('#successAskQuestionModal');
      },
      loaderColor: (customLoaderColor) ? customLoaderColor : '#fff'
    })
  })

  telInputsList.forEach($inputTel => {
    IMask($inputTel, maskOptions);
  })
};

document.addEventListener("DOMContentLoaded", () => {
  main();
});

//   
document.addEventListener("DOMContentLoaded", () => {
  createServer({
    routes() {
      this.post(
        '/api/consultation',
        function (schema, request) {
          return new Response(
            200,
            { some: 'header' },
            {
              success: true,
            },
          );
        },
        { timing: 1500 },
      );
      this.post(
        '/api/ask-question',
        function (schema, request) {
          return new Response(
            200,
            { some: 'header' },
            {
              success: true,
            },
          );
        },
        { timing: 1500 },
      );
    }
  });
});
