import Swiper from 'swiper/bundle';

export const SwiperComponent = () => {
  const swiper = new Swiper('.footer-swiper.swiper', {
    direction: 'horizontal',
    slidesPerView: 'auto',
    scrollbar: {
      el: '.footer-swiper .swiper-scrollbar',
      draggable: true,
    },
    breakpoints: {
      1200: {
        spaceBetween: 12,
        slidesPerView: 5,
        scrollbar: {
          el: '.footer-swiper .swiper-scrollbar',
          hide: false,
          draggable: true,
        },
      },
      768: {
        spaceBetween: 12,
        slidesPerView: 3,
      },
    },
  });
}
